import { Link } from "react-router-dom";

import NotFoundImage from "../assets/svg/NoMatch.svg";
import IndexNavbar from "../components/Navbar/IndexNavbar";

const NoMatch = () => {
  return (
    <>
      <IndexNavbar />
      <div className="flex justify-center  select-none bg-white h-screen">
        <div className="flex flex-col items-center pt-8 md:pt-20 lg:40 w-full">
          <img
            className="w-full  h-64 md:h-96 object-fill "
            src={NotFoundImage}
            alt=""
          />

          <div className="bg-red-600 flex justify-items-center items-center justify-center mt-6 lg:mt-14">
            <Link
              to="/"
              className="flex items-center justify-center h-8 px-4 font-medium tracking-wide bg-orange-600 text-white ring-2 ring-orange-400 border-orange-600 transition duration-200 rounded shadow-md hover:bg-white hover:text-orange-600  focus:ring-offset-2 focus:ring-orange-600 focus:bg-orange-600 focus:text-white "
              aria-label="Crete Advet"
              title="Create Advet"
            >
              <svg
                className="w-6 h-6 mr-2 "
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6"
                />
              </svg>
              Go Home
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default NoMatch;
